<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <search-box :searchData="searchData" :table="table"></search-box>

      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="25%">标题</th>
          <th width="30%">内容</th>
          <th width="15%">提交时间</th>
          <th width="15%">状态</th>
          <th width="15%">操作</th>
        </tr></thead>
        <tbody>
        <tr v-for="item in table.list">
          <td>{{item.title}}</td>
          <td class="table_content">{{item.content}}</td>
          <td>{{item.createdTime | substrTime }}</td>
          <td>{{$enums.ReplyStatus.getName(item.replyStatus)}}</td>
          <td>
            <span class="oper"
                  @click="$router.push(`/student-work/parent_mailbox/detail?id=`+item.id+``)">详情</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
    </main-part>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        nav: [{name: '家长信箱', path: ''}],
        searchData: [
          {type: 'select', title: '状态', model: 'replyStatus', option: this.$enums.ReplyStatus.list},
          {type: 'input', placeholder: '标题'},
        ],

        table: {
          url: '/mailbox/queryPatriarchMailPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            replyStatus: null,
            keywords: null
          }
        }
      }
    },
    methods: {
      // 删除活动
      async remove (item) {
        await this.$confirm(`您确认删除【${item.title}】吗?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/notice/deleteNotice.do',
          query: {
            id: item.id,
          }
        })
        this.$remind('删除成功')
        this.$search(this.table)
      }
    },
    async mounted () {
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .table_content
    padding: 0 10px
    text-overflow: ellipsis
    white-space: nowrap
    overflow: hidden
</style>
